import styles from '../styles/Hero.module.css';
import Image from 'next/image';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay, EffectFade } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/effect-fade';

const slides = [
  '/7.png',
  '/8.png',
  '/9.png',
];

export default function HeroSlider() {
  return (
    <Swiper
      modules={[Navigation, Pagination, Autoplay, EffectFade]}
      effect="fade"
      loop={true}
      autoplay={{ delay: 3000 }}
      navigation
      pagination={{ clickable: true }}
      className={styles.swiperContainer}
    >
      {slides.map((src, index) => (
        <SwiperSlide key={index}>
          <div className={styles.hero}>
            <Image
              src={src}
              alt={`Slide ${index + 1}`}
              fill
              className={styles.heroImage}
              style={{ objectFit: 'cover' }}
              priority={index === 0}
            />
            <div className={styles.overlay}>
              <h1>Cafe Furniture</h1>
              <p>
                Indulge in the ultimate cafe experience with our carefully selected
                collection of furniture, designed to elevate your space and create the
                perfect coffeehouse vibe.
              </p>
              <button className={styles.cta}>View Collection</button>
            </div>
          </div>
        </SwiperSlide>
      ))}
    </Swiper>
  );
}
